.photo-select-wrapper {
    min-height: 100vh;
    width: 100%;
    padding: 24px 20px 96px;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 24px;
    overflow: hidden;
}

.slick-slide div {
    display: flex;
    justify-content: center;
    align-items: center;
}

.slick-slider {
    width: 100%;
    max-width: 780px;
}

.photo-wrapper {
    position: relative;
    padding: 6px;
}

.photo-img {
    width: 140px;
    height: 140px;
    object-fit: cover;
    border-radius: 10px;
    transition: 0.2s;
    box-shadow: 0 6px 18px rgba(0, 0, 0, 0.08);
}

.photo-img.selected {
    opacity: 0.35;
}

.select-number {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 40px;
    font-weight: bold;
    color: white;
    text-shadow: 0 2px 8px rgba(0, 0, 0, 0.35);
}

.next-btn {
    background-color: white;
    border: none;
    font-size: 32px;
    top: 20px;
    right: 20px;
    position: fixed;
    padding: 10px 20px;
    border-radius: 12px;
    box-shadow: 0 6px 18px rgba(0, 0, 0, 0.12);
}
