.frame-select-wrapper {
    min-height: 100vh;
    width: 100%;
    padding: 24px 20px 96px;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 24px;
    overflow: hidden;
}

.slick-slide div {
    display: flex;
    justify-content: center;
    align-items: center;
}

.slick-slider {
    width: 100%;
    max-width: 780px;
}

.frame-wrapper {
    position: relative;
    padding: 6px;
}

.frame-thumbnail {
    width: 140px;
    height: 140px;
    object-fit: cover;
    border-radius: 10px;
    transition: 0.2s;
    box-shadow: 0 6px 18px rgba(0, 0, 0, 0.08);
}

.frame-thumbnail.selected {
    opacity: 0.4;
}

.check-btn {
    background-color: white;
    border: none;
    font-size: 32px;
    top: 20px;
    right: 20px;
    position: fixed;
    padding: 10px 20px;
    border-radius: 12px;
    box-shadow: 0 6px 18px rgba(0, 0, 0, 0.12);
}

.check-btn.active {
    opacity: 1;
    cursor: pointer;
}

.check-btn:disabled {
    cursor: not-allowed;
}
