/* Home Screen */
.home-container {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    padding-bottom: 50px;
    background-color: #c9b896;
    background-size: cover;
    background-position: center;
    box-sizing: border-box;
    position: relative;
    overflow: hidden;
}

/* Snow effect */
.snow-container {
    position: absolute;
    inset: 0;
    pointer-events: none;
}

.snow {
    position: absolute;
    top: -10px;
    width: 6px;
    height: 6px;
    background: #fff;
    border-radius: 50%;
    animation: fall linear infinite;
    box-shadow: 0 0 5px rgba(255, 255, 255, 0.5);
}

@keyframes fall {
    0% {
        transform: translateY(-10px) translateX(0);
    }

    100% {
        transform: translateY(100vh) translateX(20px);
    }
}

/* Bottom section */
.bottom-section {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 16px;
    z-index: 10;
}

.start-btn {
    padding: 14px 40px;
    font-size: 18px;
    font-weight: 600;
    color: #fff;
    background: rgba(0, 0, 0, 0.5);
    border: 2px solid #fff;
    border-radius: 30px;
    cursor: pointer;
    backdrop-filter: blur(5px);
    transition: all 0.2s;
}

.start-btn:hover {
    background: rgba(0, 0, 0, 0.7);
    transform: scale(1.05);
}

.credits {
    margin: 0;
    font-size: 13px;
    color: #fff;
    text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.5);
}